import request from '@/utils/request'

export function getFileApi(data) {
  return request({
    url: '/proxy_dev/supportapi/account/list', 
    method: 'get',
    params: data
  })
}

export function uploadFile(data) {
  return request({
    url: '/proxy_dev/supportapi/account/list', 
    method: 'post',
    params: data
  })
}