<template>
  <div>
    <div class="pt-6 pb-6"><span class="text-[#F56C6C]">*</span>{{title}}</div>
    <div class="border border-dashed border-[#cfd2db]">
      <el-row>
        <el-col :span="2">
          <div class="py-4 px-6 font-bold">ID</div>
        </el-col>
        <el-col :span="18">
          <div class="py-4 px-6 font-bold">指标</div>
        </el-col>
        <el-col :span="4">
          <div class="py-4 px-6 font-bold">操作</div>
        </el-col>
      </el-row>
      <el-row :class="{ 'bg-[#eff4f5]': i % 2 == 0 }" v-for="(item, i) of profileList" :key="i">
        <el-col :span="2">
          <div class="py-3 px-6 leading-10">{{ i + 1 }}</div>
        </el-col>
        <el-col :span="18">
          <div class="py-3 px-6">
            <el-input v-model="item.content" placeholder="请输入内容"></el-input>
          </div>
        </el-col>
        <el-col :span="4">
          <div class="py-3 px-6 leading-10 text-[#ec6b73] cursor-pointer" @click="onDeleteProfile(i)">删除
          </div>
        </el-col>
      </el-row>
    </div>
    <div class="border border-dashed border-[#cfd2db] mt-[30px] py-3 flex items-center justify-center cursor-pointer"
      @click="onAddProfile">
      <img src="@/assets/add.png" class="h-4 mr-2" />
      添加数据
    </div>
  </div>
</template>

<script>
export default {
  props: {
    title: { type: String },
  },
  data() {
    return {
      profileList: [{
        content: "",
      }, {
        content: "",
      }, {
        content: "",
      }, {
        content: "",
      }],
    }
  },
  methods: {
    onDeleteProfile(i) {
      if (this.profileList.length > 4) {
        this.profileList.splice(i, 1)
      } else {
        this.$message.warning("最少需要填写四个一般资料！")
      }
    },
    onAddProfile() {
      this.profileList.push({
        content: ""
      })
    },
  }
}
</script>